//------------------------------------------------------------------------------
// BASE.SCSS
//
//------------------------------------------------------------------------------


html,
body {
  width: 100%;
  height: 100%;
}

html {
  box-sizing: border-box;
  font-family: $system-font-family;
  font-size: $base-font-size;
}

body {
  margin: 0;
  padding: 0;

  background: white;

  color: $defaultcolor;
  font-family: $base-font-family;
  font-weight: 400;
  font-size: rem(16);
  line-height: 1.5;
  overflow-y: auto;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

*:focus {
  outline: 0;
}

.wrapper {
  max-width: $container-max;
  min-height: 100%;
  margin: 0 auto;
  overflow: hidden;

  background: white;
}

.off-canvas {}

.container,
%inner {
  max-width: $container-m;
  margin: 0 auto;
  padding: 0 rem(20);

  @include mq(xl) {
    padding: 0 rem(120);
  }
  
  @include mq(l){
    padding: 0 rem(80);
  }

  @include mq(m){
    padding: 0 rem(40);
  }

  @include mq(s){
    padding: 0 rem(20);
  }

  &.container-s,
  &.container-small {
    max-width: $container-s;
  }
  &.container-l,
  &.container-large {
    max-width: $container-l;
  }
  .container {
    padding: 0;
  }
}

details > * {
  box-sizing: border-box;
}

*::selection {
  color: #FFF;
  background-color: $defaultcolor;
}
